
import Ribbon from '../Ribbon.vue'
import {reactive} from 'vue'

export default {
  components: {
    Ribbon
  },
 props:{
    headtitle:{
        type:String,
    },
     eye_positionLeft:{
         type:String,
         default:'18'
     },
     eye_positionTop:{
         type:String,
         default:'45'
     },
    positionTop:{
        type:String,
       
    },
    positionLeft:{
        type:String,
        default:'300'
    },
    itemsY:{
        type:Array,
        require:true
    },
    itemsX:{
        type:Array,
        require:true       
    }
 },
   setup() {
        let numArr:any = reactive([])
        function openEyes(x:any,y:any){           
            if(numArr[x][y] === '1'){
                return true
            }else{
                return false
            }                       
        }
        rendomEyes()
        function rendomEyes(){
            numArr.length = 0
            console.log(numArr,'before')
            var x:any = Math.floor(Math.random()* 2**20) 
            var twoX = x.toString("2")
            twoX = twoX.length !== 20? '0'.repeat(20-twoX.length) + twoX : twoX        
            for(var i = 0 ; i < 5 ; i++){
                if(i === 0){
                    numArr.push(twoX.slice(i,4))                
                }else{
                    numArr.push(twoX.slice(i * 4 , (1 + i) * 4))
                }           
            }
        }
        setInterval(() => {
            rendomEyes()
        },4500)
    return {openEyes}
  }
}
